/* ============================================================
   FRONT HERO â€” NODE 75 ONLY
   - Starfield background
   - Hero layout
   - Glowing nav + button
   - Mobile responsive
=========================================================== */


/* Base reset for homepage node */
/* FINAL â€” FORCE BACKGROUND OVER EVERYTHING */
body.path-node-75 {
  background:none !important;
}


/* Kill old purple background on this wrapper only */
body.path-node-75 .homepage-hero {
  background: transparent !important;
}

/* Hide Skip link visually (still in DOM) */
body.path-node-75 .visually-hidden-focusable {
  position: absolute;
  left: -9999px;
}

/* ------------------------------------------------------------
   STARFIELD BACKGROUND (FULL VIEWPORT)
------------------------------------------------------------ */
body.path-node-75::before {
  content:"" !important;
  position:fixed !important;
  inset:0 !important;
  background:#000 url("../images/sparkle_glow_light_background.jpg") center/cover no-repeat !important;
  opacity:1 !important;
  z-index:-999 !important;
}

@keyframes starfieldFloat {
  0% {
    transform: scale(1) translate(0, 0);
  }
  100% {
    transform: scale(1.2) translate(-120px, -90px);
  }
}

/* ------------------------------------------------------------
   HERO LAYOUT
------------------------------------------------------------ */
body.path-node-75 .hero-section {
  position: relative;
  min-height: 100vh;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 4rem;
  padding: 0 8%;
  box-sizing: border-box;
}

/* LEFT TEXT */
body.path-node-75 .hero-content {
  max-width: 520px;
  color: #fff;
}

body.path-node-75 .hero-content h1 {
  font-size: 3.2rem;
  margin-bottom: 0.75rem;
  font-weight: 700;
}

body.path-node-75 .hero-content h1 .highlight-name {
  color: #0ca383;
  text-shadow:
    0 0 6px rgba(12, 163, 131, 0.9),
    0 0 18px rgba(12, 163, 131, 0.7),
    0 0 30px rgba(12, 163, 131, 0.6);
}

body.path-node-75 .hero-content h2 {
  font-size: 1.9rem;
  margin-bottom: 1.2rem;
  font-weight: 400;
}

body.path-node-75 .hero-content p {
  font-size: 1.05rem;
  color: #dddddd;
}

.about-btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1.25rem;

  padding: 1.1rem 2.8rem;
  border-radius: 999px;
  border: 2px solid #00e383;

  background: transparent;
  color: #ffffff;
  font-size: 1.2rem;
  font-weight: 600;
  text-decoration: none;

  overflow: hidden;
  cursor: pointer;
  transition: border-color .3s ease, box-shadow .3s ease;
}

.about-btn__label {
  position: relative;
  z-index: 3;
  white-space: nowrap;
}

.about-btn__icon {
  position: absolute;
  top: 50%;
  right: 0.6rem;

  width: 2.7rem;
  height: 2.7rem;
  background: #00e383;
  border-radius: 50%;

  display: flex;
  align-items: center;
  justify-content: center;

  transform: translate(0, -50%);
  transition: transform .35s ease, box-shadow .35s ease;
}

.about-btn__icon i {
  color: #ffffff;
  font-size: 1.2rem;
}

.about-btn:hover {
  border-color: #00e383;
  box-shadow: 0 0 16px rgba(0, 227, 131, .55);
}

.about-btn:hover .about-btn__icon {
  transform: translate(-0.35rem, -50%);
  box-shadow: 0 0 18px rgba(0, 227, 131, .75);
}

/* ------------------------------------------------------------
   HERO IMAGE â€” CENTER COLUMN
------------------------------------------------------------ */
body.path-node-75 .hero-media img {
  width: 520px;
  max-width: 100%;
  border-radius: 28px;
  display: block;
  box-shadow:
    0 0 30px rgba(12, 163, 131, 0.7),
    0 0 70px rgba(12, 163, 131, 0.5);
}

/* ------------------------------------------------------------
   RIGHT VERTICAL NAV
------------------------------------------------------------ */
body.path-node-75 .vertical-nav {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

body.path-node-75 .vertical-nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 2rem; /* spacing between items */
}

body.path-node-75 .vertical-nav a {
  position: relative;
  display: inline-block;
  width: 260px;
  padding: 0.9rem 0;
  border-radius: 999px;
  border: 2px solid #0ca383;
  text-align: center;
  color: #fff;
  background: #000;
  text-decoration: none;
  font-size: 1.1rem;
  overflow: hidden;
  box-shadow:
    0 0 16px rgba(12, 163, 131, 0.7),
    0 0 40px rgba(12, 163, 131, 0.4);
  transition: color 0.35s ease, transform 0.2s ease;
}

/* Fill animation top â†’ bottom */
body.path-node-75 .vertical-nav a::before {
  content: "";
  position: absolute;
  inset: 0;
  background: #0ca383;
  transform: translateY(-110%);
  transition: transform 0.35s ease;
  z-index: -1;
}

body.path-node-75 .vertical-nav a:hover::before {
  transform: translateY(0);
}

body.path-node-75 .vertical-nav a:hover {
  color: #000;
  transform: translateY(-1px);
}

/* Active state â€“ keep filled */
body.path-node-75 .vertical-nav a.is-active {
  color: #000;
}

body.path-node-75 .vertical-nav a.is-active::before {
  transform: translateY(0);
}

/* ------------------------------------------------------------
   RESPONSIVE / MOBILE
------------------------------------------------------------ */
@media (max-width: 960px) {
  body.path-node-75 {
    overflow: auto; /* allow scroll on small screens */
  }

  body.path-node-75 .hero-section {
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 4rem 7% 3rem;
    gap: 3rem;
  }

  body.path-node-75 .hero-media img {
    width: 360px;
  }

  body.path-node-75 .vertical-nav {
    align-items: center;
  }

  body.path-node-75 .vertical-nav a {
    width: 260px;
  }
}
/* ============================================================
   ðŸ”¥ FINAL OVERRIDE â€” STARFIELD ALWAYS WINS (NODE 75 ONLY)
===========================================================*/
body.path-node-75 {
  background:none !important;
  position:relative !important;
}

body.path-node-75::before {
  content:"" !important;
  position:fixed !important;
  inset:0 !important;
  background:#000 url("../images/sparkle_glow_light_background.jpg") center/cover no-repeat !important;
  opacity:1 !important;
  z-index:-999999 !important;
  animation:starsFloat 40s linear infinite !important;
}

@keyframes starsFloat {
  0% { transform:scale(1) translate(0,0); }
  100% { transform:scale(1.2) translate(-120px,-90px); }
}

/* Removes purple bleed from wrapper if still present */
.homepage-hero {
  background:none !important;
}
